import { signIn } from "@/lib/auth";

const GoogleSignin = () => {
  return (
    <form
      action={async () => {
        "use server";
        await signIn("google");
      }}
    >
      <button className="bg-slate-50 p-2 rounded" type="submit">Signin with Google</button>
    </form>
  );
};

export default GoogleSignin;
