import GoogleSignin from "@/components/GoogleSignin";
import { auth } from "@/lib/auth";

export default async function Home() {
  const session = await auth();
  console.log(`homepage`, session);

  if (!session?.user) {
    return (
      <div className="bg-slate-900 mx-auto rounded mt-10 max-w-3xl p-5">
        <GoogleSignin />
      </div>
    );
  }

  return (
    <div className="bg-slate-900 mx-auto rounded mt-10 max-w-3xl p-5 text-slate-50 flex justify-between">
      <div>
        <h1 className="text-3xl mb-3">Welcome Back</h1>
        <h1 className="my-2">{session.user.name}</h1>
        <p className="text-sm">{session.user.email}</p>
      </div>
      <div>
        <img className="rounded-[50%]" src={session.user.image as string} alt="sdasd" />
      </div>
    </div>
  );
}
